const StackData = {
    id: 'stack',
    lightBg: true,
    ligthText: false,
    topLine: 'Stack',
    headLine: 'Stack tecnológicos',
    description: 'He utilizado los siguientes lenguajes, herramientas y frameworks durante mi trayectoria profesional',
    imgStart: false,
    //img: NetNodeImg,
    alt: 'stack',
    primary: true,
    darkText: true,
    children: (<>
        <TechStacks />
    </>)
}

export default StackData;