import { Grid } from "@material-ui/core";
import React from "react";
import styled, { css } from 'styled-components'


const GridStackItem = styled(Grid)`
    &&&{
        /* text-align: center;
        display: flex;
        align-content: center;
        flex-wrap: nowrap;
        justify-content: center;
        height: 13vh;
        border: 1px solid black;
        border-radius: 21px; */
        font-size: 4.5em !important;
        margin: 15px !important;
        padding: 10px !important;
        opacity: 0.93 !important;
        border: 1.7px solid rgba(200, 137, 230, 0.637) !important;
        vertical-align: middle !important;
        text-align: center !important;
        border-radius: 5px !important;
        display: table !important;
        box-shadow: 4px 5px 4px 3px rgb(89 4 168 / 14%) !important;
        overflow: hidden !important;
        transition: all 0.4s ease 0s !important;
    }
`

const GridStackItemWrapper = styled.div`
   display: flex;
   align-items: center;
   font-size: 6rem;
`


const StackItem = ({ xs, md, children }) => (
    <GridStackItem item xs={xs} md={md} className="stack-icons">
        {/* <GridStackItemWrapper> */}
        {children}
        {/* </GridStackItemWrapper> */}
    </GridStackItem>
);


export default StackItem;